footer {
  	background-color: #021C3F;
    padding: 50px 0;
    .logo {
      @media(max-width: 1024px) {
        margin-bottom: 33px;
      }
    }
  .footer-wrap {
    display: inline-flex;
    width: 100%;
    justify-content: space-between;
    font-size: 14px;
    color: #fff;
    @media(max-width: 1024px) {
      align-items: flex-start;
    }
    @media(max-width: 767px) {
      flex-direction: column;
      align-items: center;
    }
    &:last-child {
      padding-top: 48px;
      @media(max-width: 767px) {
        padding-top: 15px;
      }
    }
  }
  .col-10 {
    @media(max-width: 1024px) {
      max-width: 100%;
      margin: 0px;
    }
  }
  ul {
    display: inline-flex;
    margin-left: 100px;
    li {
      @media(max-width: 767px) {
        margin-top: 40px;
      }
      &:first-child {
        a {
          margin-left: 0px !important;
        }
      }
      a {
        font-size: 16px;
        font-weight: bold;
        color: #fff;
        margin: 0 25px;
        transition: .3s;
        @media(max-width: 1024px) {
          &:last-child {
            margin-right: 0px;
          }
        }
        &:hover {
          color: #0158CA;
          transition: .3s;
        }
      }
    }
    @media(max-width: 1199px) {
      margin-left: 40px;
    }
    @media(max-width: 1024px) {
      margin: 10px 0;
    }
  }
  .instagram {
    &:hover {
      color: #fccc63;
    }
  }
  .youtube {
    &:hover {
      color: #dc3545;
    }
  }
  .twitter {
    &:hover {
      color: #1dcaff;
    }
  }
  .facebook {
    &:hover {
      color: #3B5998;
    }
  }
  .right {
    svg {
      margin: 0 20px;
      font-size: 24px;
      color: #fff;
      @media(max-width: 1024px) {
        &:first-child {
          margin-left: 0px;
        }
      }
      @media(max-width: 1024px) {
        margin: 0 20px !important;
      }
    }
  }
  .left {
    display: inline-flex;
    align-items: center;
    @media(max-width: 1024px) {
      align-items: flex-start;
      flex-direction: column;
    }
    @media(max-width: 767px) {
      align-items: center;
    }
  }
}
