.user {
  display: inline-flex;
  align-items: flex-start;
  margin-top: 82px;
  margin-bottom: 82px;
  position: relative;
  @media(max-width: 767px) {
    padding: 0px !important;
    flex-direction: column;
  }
  &.slide {
    flex-direction: column;
    padding: 0px 24px 50px !important;
    background-color: transparent !important;
    border-bottom: none !important;
    text-align: center;
    align-items: center;
    @media(max-width: 1024px) {
      max-width: 100% !important;
      .user-text {
        max-width: 310px;
      }
    }
    img {
      width: 150px;
      height: 150px;
    }
    div {
      margin: 0px;
      text-align: center;
    }
    .possition {
      justify-content: center;
      &:after {
        margin: 0 auto;
        left: 0px;
        right: 0px;
      }
    }
  }
  &.blue {
    margin: 0px;
    border-radius: 2px;
    width: 100%;
    padding: 24px;
    border-bottom: solid 3px #0158CA;
    background-color: #E5EEF9;
    @media(max-width: 1024px) {
      max-width: 80%;
      margin-left: 10%;
    }
    @media(max-width: 767px) {
      max-width: 100%;
      margin-left: 0px;
      padding: 20px !important;
    }
    &.on-blue {
      background-color: #DDEBFC;
    }
    .name {
      color: #4A4A4A;
    }
    .possition {
      color: #0158CA;
      cursor: pointer;
      display: flex;
      align-items: center;
      img {
        margin-right: 5px;
        width: 20px;
        height: 20px;
      }
      &:after {
        background: #0158CA;
      }
    }
    .user-text {
      color: #0158CA;
    }
    &:after {
      display: none;
    }
  }
  &:after {
    content: '';
    position: absolute;
    width: calc(200% + 50px);
    height: calc(100% + 100px);
    background: rgba(255, 255, 255, 0.1);
    left: -100%;
    top: -50px;
    border-bottom: solid 1px #fff;
    border-radius: 0 2px 2px 0;
    @media(max-width: 1199px) {
      width: calc(200% + 15px);
    }
    @media(max-width: 767px) {
      width: calc(100% + 40px) !important;
      left: -20px !important;
    }
  }
  div {
    margin-left: 30px;
    @media(max-width: 767px) {
      margin: 0px;
    }
  }
  .name {
    font-size: 20px;
    font-weight: bold;
    line-height: 24px;
    color: #fff;
  }
  .date {
    color: #4A4A4A;
    font-size: 12px;
    margin-top: 20px;
  }
  .possition {
    font-size: 16px;
    line-height: 19px;
    color: #fff;
    margin-top: 4px;
    position: relative;
    &:after {
      width: 30px;
      height: 4px;
      background: #fff;
      position: absolute;
      content: '';
      left: 0px;
      bottom: -10px;
      border-radius: 2px;
    }
  }
  .user-text {
    font-size: 16px;
    color: #fff;
    margin-top: 34px;
    @media(max-width: 1199px) {
      font-size: 14px;
    }
    @media(max-width: 1024px) {
      font-size: 16px;
    }
    @media(max-width: 767px) {
      font-size: 14px;
    }
  }
}
