.hero-section {
  background: linear-gradient(314.65deg, #0158CA 0%, #094D9B 100%);
  padding: 0 0 90px;
  .button-with-arrow {
    @media(max-width: 1024px) {
      display: none;
    }
  }
  .user {
    @media(max-width: 1024px) {
      padding-left: 50px;
      &:after {
        width: calc(200% + 70px);
      }
    }
  }
  .block-with-image {
    @media(max-width: 767px) {
      margin-top: 43px;
    }
  }
  .row {
    @media(max-width: 1024px) {
      flex-direction: column-reverse;
      .col-5 {
        width: 100%;
        max-width: 100%;
        margin: 0px;
      }
      .block-with-image {
        width: 100%;
        max-width: 100%;
        flex: 1;
      }
    }
  }
  .title {
    font-size: 36px;
    font-weight: bold;
    line-height: 43px;
    color: #fff;
    margin-top: 62px;
    @media(max-width: 1199px) {
      font-size: 28px;
    }
    @media(max-width: 1024px) {
      font-size: 36px;
      margin-top: 30px;
    }
  }
  .subtitle {
    font-size: 24px;
    font-weight: bold;
    line-height: 28px;
    color: #fff;
    @media(max-width: 1199px) {
      font-size: 18px;
    }
    @media(max-width: 1024px) {
      display: none;
    }
  }
  .main-text {
    font-size: 20px;
    color: #fff;
    padding-top: 20px;
    @media(max-width: 1199px) {
      font-size: 16px;
    }
    @media(max-width: 1024px) {
      font-size: 20px;
    }
    @media(max-width: 767px) {
      font-size: 16px;
    }
  }
  .button-with-arrow {
    max-width: 300px;
    margin-top: 25px;
  }
}
