.illustration {
  background: linear-gradient(314.65deg, #0158CA 0%, #094D9B 100%);
  display: block;
  padding: 130px 0 150px;
  @media(max-width: 1024px) {
    padding: 50px 0 90px;
  }
  .ill-mobile {
    display: none;
    @media(max-width: 1024px) {
      display: block;
      width: 100%;
      max-width: 450px;
      margin: 0 auto;
      padding: 40px 0;
    }
  }
  .row {
    @media(max-width: 1024px) {
      flex-direction: column;
      .col-5 {
        max-width: 100%;
        margin: 0px;
        width: 80%;
        margin-left: 10%;
      }
    }
    @media(max-width: 767px) {
      .col-5 {
        max-width: 100%;
        margin: 0px;
        width: 100%;
      }
    }
  }
  .title {
    color: #FFFFFF;
    font-size: 32px;
    font-weight: bold;
  }
  .text {
    color: #FFFFFF;
    font-size: 20px;
    padding-top: 44px;
  }
  .time {
    color: #FFFFFF;
    font-size: 32px;
    font-weight: bold;
    padding-top: 30px;
  }
  .time-row {
    display: inline-flex;
    .timer {
      width: 96px;
      height: 96px;
      background: rgba(255,255,255, .10);
      border-radius: 2px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 32px;
      font-weight: bold;
      color: #fff;
      border-bottom: solid 2px #fff;
      margin-right: 20px;
      &.opacity {
        opacity: .3;
      }
    }
  }
  .time-under-row {
    width: 100%;
    display: inline-flex;
    .timer {
      font-size: 14px;
      font-weight: bold;
      color: #fff;
      margin: 10px 20px 0 0;
      width: 96px;
      display: flex;
    }
  }
  .subtitle {
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 1px;
    color: #fff;
    margin: 40px 0 20px;
  }
  .ill {
    width: 100%;
    padding: 60px 0 0;
    position: absolute;
    top: -200px;
    padding: 0px;
    width: 100%;
    @media(max-width: 1024px) {
      display: none;
    }
  }
  .left {
    padding-right: 70px;
    @media(max-width: 1024px) {
      padding: 0px;
    }
  }
  .button-with-arrow {
    text-transform: uppercase;
    max-width: 300px;
    img {
      margin-left: 72px;
    }
  }
}
