.main-form {
  background-image: url('../img/form.jpg');
  min-height: 100vh;
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 90px 0 130px;
  position: relative;
  @media(max-width: 1024px) {
    background-image: url('../img/form2.jpg');
  }
  &.register {
    @media(max-width: 1024px) {
      background-image: url('../img/form2.jpg');
    }
    @media(max-width: 767px) {
      background-image: none !important;
    }
    .button {
      margin-top: 30px;
    }
    .new-account {
      margin-top: 20px;
      margin-bottom: 0px;
      font-size: 18px;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  &.forgot-password {
    .new-account {
      border-top: solid 1px #D8D8D8;
      padding-top: 30px;
    }
  }
  @media(max-width: 767px) {
    padding: 0px;
    background: none;
  }
  .container {
    @media(max-width: 767px) {
      max-width: 100%;
      padding: 0px;
    }
  }
  .col-6 {
    @media(max-width: 1024px) {
      max-width: 70%;
      width: 70%;
      margin-left: 15%;
      flex: 0 0 70%;
    }
    @media(max-width: 767px) {
      max-width: 100%;
      width: 100%;
      flex: 0 0 100%;
      margin: 0px;
    }
  }
  .col-10 {
    @media(max-width: 767px) {
      max-width: 100%;
      width: 100%;
      flex: 0 0 100%;
      margin: 0px;
    }
  }
  .f-card {
    background: #fff;
    padding: 40px 50px 26px;
    border: 1px solid #0158CA;
    box-shadow: 0 4px 24px 0 rgba(0,0,0,0.21);
    border-radius: 4px;
    margin-top: 50px;
    @media(max-width: 767px) {
      box-shadow: none;
      border: none;
      border-radius: 0px;
      margin-top: 0px;
    }
    .title {
      color: #0158CA;
      font-size: 24px;
      font-weight: bold;
      text-align: center;
      text-transform: uppercase;
    }
    .subtitle {
      color: #0158CA;
      font-size: 14px;
      line-height: 17px;
      text-align: center;
      margin: 10px 0 50px;
    }
  }
  .logo {
    text-align: center;
    width: 100%;
    @media(max-width: 767px) {
      background-image: url(../img/mobbg.png);
      height: 72px;
      background-size: cover;
      background-position: center center;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        width: 210px;
      }
    }
  }
  .forgot {
    color: #021C3F;
    font-size: 12px;
    margin-top: 10px;
    border-bottom: solid 1px #D8D8D8;
    padding-bottom: 20px;
    a {
      color: #021C3F;
      text-decoration: underline;
    }
  }
  .new-account {
    color: #0158CA;
    font-size: 14px;
    margin: 30px 0 20px;
    display: block;
    text-align: center;
  }
}
.f-container {
  margin-bottom: 20px;
  label {
    color: #021C3F;
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 10px;
  }
  svg {
    color: #9B9B9B;
    font-size: 20px;
    position: absolute;
    top: 18px;
    left: 17px;
  }
  .f-text {
    color: #9B9B9B;
    font-size: 12px;
    line-height: 14px;
    margin-top: 10px;
  }
  .f-row {
    position: relative;
    input {
      box-shadow: none;
      margin: 0px;
      border: 1px solid #ADACAC;
      border-radius: 4px;
      width: 100%;
      height: 58px;
      padding: 0 20px 0 48px;
      font-size: 18px;
      &::placeholder {
        color: #D3D3D3;
      }
      &:focus {
        outline: none;
        border: solid 1px #0158CA;
        + svg {
          color: #0158CA;
        }
      }
    }
  }
}







/* The container */
.container-check {
    display: block;
    position: relative;
    padding-left: 35px;
    cursor: pointer;
    color: #021C3F;
    font-size: 18px;
    margin: 0px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* Hide the browser's default checkbox */
.container-check input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: rgba(133, 194, 66, 0.2);
    border-radius: 2px;
}


/* When the checkbox is checked, add a blue background */
.container-check input:checked ~ .checkmark {
    background-color: rgba(133, 194, 66, 0.2);
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.container-check input:checked ~ .checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.container-check .checkmark:after {
    left: 9px;
    top: 4px;
    width: 8px;
    height: 13px;
    border: solid #85C242;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    border-radius: 2px;
}
.check {
    margin-bottom: 20px;
  .check-text {
    color: #9B9B9B;
    font-size: 12px;
    padding: 7px 0 0 34px;
  }
}
