.br-mobile {
  display: none;
  @media(max-width: 767px) {
    display: block;
  }
}
body, html {
  @media(max-width: 767px) {
    overflow-x: hidden !important;
  }
}
.show-mobile {
  display: none;
  @media(max-width: 1024px) {
    display: flex;
  }
}
.hide-mobile {
  display: flex;
  @media(max-width: 1024px) {
    display: none;
  }
}
.mobile-reverce {
  @media(max-width: 1024px) {
    flex-direction: column-reverse !important;
  }
}

.c-green {
  color: #85C242;
}


.inline-row {
  display: inline-flex;
  width: 100%;
  justify-content: space-between;
  .card-secondary {
    width: 100%;
    display: inline-flex;
  }
  @media(max-width: 1024px) {
    flex-direction: column;
    .f-container {
      width: 100% !important;
    }
  }
  &.cards-n {
    justify-content: flex-start;
  }
  .f-container {
    width: calc(50% - 12px);
    &.card-n {
      min-width: calc(50% - 12px);
      margin-right: 24px;
    }
    &.date {
      max-width: 90px;
      margin-right: 20px;
      input {
        padding: 0px;
        text-align: center;
        max-width: 90px;
      }
    }
  }
}


.copyright {
  color: #021C3F;
  font-size: 18px;
  position: absolute;
  bottom: 30px;
  left: 0px;
  right: 0px;
  margin: auto;
  text-align: center;
  @media(max-width: 767px) {
    position: relative;
    margin-top: 20px;
  }
  span {
    font-weight: bold;
  }
}


.bold {
  font-weight: bold;
}
