.pricing {
  	background: linear-gradient(314.65deg, #0158CA 0%, #094D9B 100%);
    padding-bottom: 59px;
    position: relative;
    z-index: 1;
    &:before {
      position: absolute;
      content: '';
      width: 504px;
      height: 680px;
      background: rgba(255,255,255, .1);
      z-index: -1;
      top: 180px;
      right: 0px;
      border-radius: 4px 0 0 4px;
      @media(max-width: 1024px) {
        top: 270px;
      }
      @media(max-width: 767px) {
        display: none;
      }
    }
    .col-10  {
      @media(max-width: 767px) {
        width: 100%;
        margin: 0px;
        max-width: 100%;
      }
    }
    .row {
      @media(max-width: 1024px) {
        flex-direction: column;
        .col-6 {
          max-width: 70%;
          margin-left: 15%;
        }
      }
    }
    .row {
      @media(max-width: 767px) {
        .col-6 {
          max-width: 100%;
          margin: 0px;
          width: 100%;
          flex: 1;
        }
      }
    }
    .top-title-row {
      display: inline-flex;
      justify-content: space-between;
      width: 100%;
      margin-top: 50px;
      @media(max-width: 1024px) {
        flex-direction: column;
      }
      .top-title {
        font-size: 36px;
        font-weight: bold;
        color: #fff;
        position: relative;
        &::after {
          content: '';
          position: absolute;
          background: #fff;
          width: 30px;
          height: 4px;
          border-radius: 2px;
          bottom: -10px;
          left: 0px;
        }
        @media(max-width: 1199px) {
          font-size: 28px;
        }
        @media(max-width: 1024px) {
          font-size: 36px;
        }
        @media(max-width: 767px) {
          font-size: 24px;
        }
      }
      .top-subtitle {
        font-size: 48px;
        font-weight: bold;
        color: #fff;
        @media(max-width: 1199px) {
          font-size: 28px;
          font-size: 42px;
        }
        @media(max-width: 1024px) {
          text-align: right;
          font-size: 48px;
          margin-top: 36px;
        }
        @media(max-width: 767px) {
          font-size: 26px;
          text-align: left;
        }
      }
    }
    .p-card {
      margin-top: 67px;
      border: 1px solid #0158CA;
      border-radius: 4px;
      background-color: #FFFFFF;
      box-shadow: 0 8px 36px 0 rgba(0,0,0,0.15);
      text-align: center;
      padding: 34px 0 0;
      position: relative;
      overflow: hidden;
      z-index: 2;
      width: calc(100% - 25px);
      &.last {
        float: right;
      }
      &:before {
        background-image: url(../img/card.svg);
        position: absolute;
        content: '';
        width: 100%;
        height: 300px;
        left: 0px;
        background-size: cover;
        top: 0px;
        z-index: -1;
        background-position: center center;
      }
      @media(max-width: 1024px) {
        width: 100%;
        margin-top: 60px;
      }
      .title {
        color: #1C384A;
        font-size: 36px;
        font-weight: bold;
        letter-spacing: 1px;
        line-height: 43px;
      }
      .subtitle {
        color: #1C384A;
        font-size: 18px;
        line-height: 22px;
        padding: 20px 55px 0;
      }
      .p-badge {
        background: url('../img/123.svg');
        color: #0158CA;
      	font-size: 14px;
        text-align: center;
        position: absolute;
        top: 0px;
        right: 0px;
        width: 195px;
        height: 59px;
        background-repeat: no-repeat;
        display: flex;
        align-items: center;
        justify-content: center;
        @media(max-width: 1199px) {
          z-index: -1;
          width: 150px;
        }
      }
      .products {
        border-radius: 4px;
        background-color: #021C3F;
        height: 74px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        font-weight: bold;
        color: #fff;
        width: calc(100% - 110px);
        margin: 0 auto;
        margin-top: 30px;
        margin-bottom: 32px;
      }
      ul {
        padding: 0 55px;
        li {
          display: inline-flex;
          align-items: flex-start;
          color: #1C384A;
          font-family: Rubik;
          font-size: 18px;
          justify-content: flex-start;
          width: 100%;
          text-align: left;
          margin-bottom: 32px;
          img {
            margin-right: 16px;
          }
        }
      }
      .p-card-footer {
        background-color: #85C242;
        height: 110px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 28px;
        font-weight: bold;
        letter-spacing: 1px;
        color: #fff;
        border-radius: 0 0 4px 4px;
        span {
          font-size: 20px;
        }
      }

    }
}
.under-pricing {
  padding: 104px 0 127px;
  position: relative;
  @media(max-width: 1024px) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  .row {
    @media(max-width: 1024px) {
      flex-direction: column-reverse;
      .col-md-5 {
        width: 100%;
        max-width: 100%;
      }
    }
  }
  &:after {
    background: url(../img/map.png);
    position: absolute;
    content: '';
    width: 60%;
    height: 488px;
    top: 0px;
    background-repeat: no-repeat;
    background-size: cover;
    right: 0px;
    top: -25px;
    z-index: 10;
    @media(max-width: 1024px) {
      top: 65px;
    }
  }
  .container {
    position: relative;
    z-index: 20;
  }
  p.text {
    color: #021C3F;
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 20px;
    &:first-child {
      position: relative;
      &:after {
        width: 40px;
        background-color: #0158CA;
        height: 4px;
        border-radius: 2px;
        position: absolute;
        content: '';
        left: 0px;
        top: -40px;
      }
    }
  }
  .button-with-arrow {
    max-width: 300px;
    margin-top: 60px;
  }
  .title {
    color: #0158CA;
    font-size: 48px;
    font-weight: bold;
    @media(max-width: 1024px) {
      padding-left: 25%;
      margin-bottom: 84px;
    }
  }
}
