.block-with-image {
  margin-top: 125px;
  padding-right: 0px;
  @media(max-width: 767px) {
    margin-top: 50px;
  }
  &.green {
    p {
      color: #85C242;
    }
  }
  &.left {
    padding-left: 0px;
    padding-right: 15px;
    p {
      right: 85px;
      left: auto;
      @media(max-width: 1024px) {
        left: 30px;
      }
    }
  }
  .block-with-image-wrap {
    width: 100%;
    img {
      width: 100%;
    }
  }
  p {
    color: #fff;
    font-size: 48px;
    font-weight: bold;
    line-height: 57px;
    position: absolute;
    top: -32px;
    left: 135px;
    text-transform: uppercase;
    @media(max-width: 1199px) {
      font-size: 38px;
    }
    @media(max-width: 1024px) {
      font-size: 48px;
    }
    @media(max-width: 767px) {
      font-size: 28px;
    }
  }
}


.main-section-block {
  	background-color: #E9F4FF;
    position: relative;
    padding: 0 0 155px;
    &.without-bottom-row {
      padding: 40px 0 50px;
      @media(max-width: 1024px) {
        padding-top: 130px;
      }
    }
    &.design {
      padding-top: 50px;
      @media(max-width: 767px) {
        padding: 100px 0 155px;
      }
    }
    &.protection {
      z-index: 2;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        background: #D5EAD9;
        height: 40px;
        width: 30%;
        top: 163px;
        right: 0px;
        border-radius: 2px 0 0 2px;
        z-index: -1;
        @media(max-width: 1024px) {
          display: none;
        }
      }
      .block-with-image-wrap {
        text-align: center;
        img {
          width: 75%;
          margin-top: 80px;
          @media(max-width: 1024px) {
            margin-bottom: 40px;
          }
        }
      }
    }
    .row {
      @media(max-width: 1024px) {
        flex-direction: column;
        .col-6, .col-5 {
          max-width: 100%;
          margin-left: 0px;
        }
      }
    }
    &.white {
      background: #fff;
    }
    .title {
      font-size: 34px;
      font-weight: bold;
      line-height: 43px;
      color: #0158CA;
      margin-top: 135px;
      margin-bottom: 60px;
      @media(max-width: 1024px) {
        max-width: 80%;
        margin: 0 auto;
        margin-bottom: 30px;
      }
      @media(max-width: 767px) {
        font-size: 24px;
        line-height: 1.5em;
        max-width: 100%;
        br {
          display: none;
        }
      }

      a {
        color: #FFAB1F;
        border-bottom: solid 3px;
      }
    }
}




.check-row {
  display: inline-flex;
  align-items: flex-start;
  margin-bottom: 40px;
  @media(max-width: 1024px) {
    max-width: 80%;
    padding-left: 10%;
  }
  @media(max-width: 767px) {
    max-width: 100%;
    padding-left: 0px;
  }
  img {
    margin-right: 20px;
  }
  .check-title {
    color: #85C242;
    font-size: 20px;
    font-weight: bold;
    padding-top: 11px;
  }
  .check-text {
    color: #4A4A4A;
    font-size: 16px;
    margin-top: 10px;
  }
}


.bottom-card {
  width: 100%;
  z-index: 10;
  display: flex;
  align-items: center;
  max-height: 170px;
  height: 100%;
  border-radius: 2px;
  bottom: -85px;
  left: 0px;
  right: 0px;
  margin: auto;
  justify-content: space-between;
  padding: 0 60px;
  background: linear-gradient(314.65deg, #0158CA 0%, #094D9B 100%);
  position: absolute;
  max-width: 793px;
  @media(max-width: 1024px) {
    max-width: 100%;
  }
  @media(max-width: 767px) {
    height: auto;
    flex-direction: column;
    max-height: inherit;
    padding: 20px;
  }
  .title {
    color: #FFFFFF;
    font-size: 20px;
    letter-spacing: 1px;
    line-height: 24px;
    font-weight: 400;
    margin: 0 0 10px;
    @media(max-width: 1024px) {
      max-width: 100%;
    }
  }
  .subtitle {
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 1px;
    line-height: 24px;
    color: #fff;
    @media(max-width: 767px) {
      margin-bottom: 30px;
    }
  }
}
