.hidden-menu {
  position: absolute;
  width: 265px;
  opacity: 0.98;
  background-color: #021C3F;
  z-index: 999;
  top: 0px;
  height: 100%;
  padding: 30px 20px;
  left: -100%;
  transition: .3s;
  &.active {
    left: 0px;
    transition: .3s;
  }
  .close-btn {
    font-size: 16px;
    font-weight: bold;
    line-height: 19px;
    color: #fff;
    display: flex;
    cursor: pointer;
    align-items: center;
    img {
      margin-left: 15px;
    }
  }
}




header.mobile-header {
  display: none;
  .button-header {
    background-color: #85C242;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    display: inline-flex;
    align-items: center;
    padding: 0 20px 0 0;
    height: 48px;
    border-radius: 2px;
    cursor: pointer;
    min-height: 48px;
    line-height: 48px;
    transition: .3s;
    &:hover {
      transition: .3s;
      background: #62AC10;
    }
    span {
      margin-right: 20px;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 48px;
      min-width: 48px;
      border-radius: 2px 0 0 2px;
      background-color: #62AC10;
    }
  }
  ul {
    display: inline-flex;
    flex-direction: column;
    width: 100%;
    margin-top: 60px;
    li {
      margin-bottom: 30px;
      a {
        font-size: 24px;
        font-weight: bold;
        color: #fff;
        cursor: pointer;
        cursor: pointer;
        transition: .3s;
      }
    }
  }
  .header-wrap {
    padding: 20px;
  }
  @media(max-width: 767px) {
    display: block;
  }
}



header {
  background: rgba(255, 255, 255, .10);
  border-bottom: solid 1px #fff;
  @media(max-width: 767px) {
    display: none;
  }
  .menu-open {
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    cursor: pointer;
    img {
      margin-left: 15px;
    }
  }
  .col-10 {
    @media(max-width: 1024px) {
      margin: 0px;
      max-width: 100%;
      flex: 1;
    }
  }
  .header-wrap {
    display: inline-flex;
    width: 100%;
    justify-content: space-between;
    padding: 21px 0;
    .right {
      display: flex;
      align-items: center;
      .button-header {
        background-color: #85C242;
        font-size: 16px;
        font-weight: 600;
        color: #fff;
        display: inline-flex;
        align-items: center;
        padding: 0 20px 0 0;
        height: 48px;
        border-radius: 2px;
        cursor: pointer;
        min-height: 48px;
        line-height: 48px;
        transition: .3s;
        &:hover {
          transition: .3s;
          background: #62AC10;
        }
        span {
          margin-right: 20px;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          width: 48px;
          min-width: 48px;
          border-radius: 2px 0 0 2px;
          background-color: #62AC10;
        }
      }
      ul {
        display: inline-flex;
        li {
          a {
            font-size: 16px;
            font-weight: bold;
            color: #fff;
            cursor: pointer;
            margin: 0 50px;
            cursor: pointer;
            transition: .3s;
            @media(max-width: 1199px) {
              margin: 0 30px;
            }
            &:hover {
              transition: .3s;
              color: #021C3F;
            }
          }
        }
      }
    }
  }
}
