.slider {
  padding: 72px 0;
  position: relative;
  @media(max-width: 767px) {
    padding: 50px 0 50px;
  }
  &:after {
    height: 320px;
    width: 402px;
    opacity: 0.05;
    border-radius: 0 2px 2px 0;
    background-color: #0158CA;
    position: absolute;
    content: '';
    bottom: 65px;
    left: 0px;
    @media(max-width: 1024px) {
      width: 300px;
    }
    @media(max-width: 767px) {
      display: none;
    }
  }
  &:before {
    height: 320px;
    width: 402px;
    opacity: 0.1;
    border-radius: 2px 0 0 2px;
    background-color: #85C242;
    position: absolute;
    content: '';
    top: 154px;
    right: 0px;
    @media(max-width: 1024px) {
      width: 300px;
    }
    @media(max-width: 767px) {
      top: 250px;
    }
  }
  .text-row {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    margin-bottom: 50px;
    @media(max-width: 1024px) {
      flex-direction: column-reverse;
    }
  }
  .col-md-10 {
    @media(max-width: 1024px) {
      max-width: 100%;
      margin: 0px;
    }
  }
  .slider-title {
    color: #0158CA;
    font-size: 36px;
    font-weight: bold;
    line-height: 43px;
    position: relative;
    @media(max-width: 1024px) {
      width: 100%;
    }
    @media(max-width: 767px) {
      font-size: 24px;
      margin-top: 15px;
    }
    &:after {
      position: absolute;
      content: '';
      background: #0158CA;
      width: 30px;
      height: 4px;
      border-radius: 20px;
      bottom: -20px;
      left: 0px;
    }
  }
  .slider-subtitle {
    color: #85C242;
    font-size: 48px;
    font-weight: bold;
    text-transform: uppercase;
    @media(max-width: 1024px) {
      width: 100%;
      text-align: right;
    }
    @media(max-width: 767px) {
      font-size: 28px;
      text-align: left;
    }
  }
  .owl-nav {
    position: absolute;
    display: flex;
    width: 100%;
    justify-content: space-between;
    top: 50%;
    @media(max-width: 1024px) {
      max-width: 80%;
      left: 10%;
    }
  }
  .owl-dots {
    display: flex;
    width: 100%;
    justify-content: center;
    .owl-dot {
      border: 1px solid #85C242;
      width: 11px;
      height: 11px;
      border-radius: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 9px;
    &.active {
      span {
        background: #85C242;
        width: 7px;
        height: 7px;
        display: flex;
        border-radius: 10px;
      }
    }
    }
  }
}
