.button-with-arrow {
  box-shadow: 0 2px 12px 0 rgba(0,0,0,0.2);
  background-color: #85C242;
  font-size: 20px;
  font-weight: bold;
  color: #fff;
  padding: 16px 17px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .3s;
  &:hover {
    color: #fff;
    background: #62AC10;
    transition: .3s;
  }
  img {
    margin-left: 18px;
  }
}

.button {
  border-radius: 2px;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,0.2);
  color: #FFFFFF;
  font-size: 20px;
  font-weight: bold;
  display: block;
  height: 58px;
  line-height: 58px;
  text-align: center;
  &:hover {
    color: #fff;
  }
  &.green {
    background-color: #85C242;
    transition: .3s;
    &:hover {
      background: #79AE3B;
      transition: .3s;
    }
  }
  &.dark {
    background-color: #021C3F;
    transition: .3s;
    &:hover {
      background: #14315B;
      transition: .3s;
    }
  }
}
